@import url(https://fonts.googleapis.com/css?family=Montserrat:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


  .container{
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 10px;
      grid-auto-columns: 500px;
      border: 7px solid white;
      overflow: hidden;
      background-color:whitesmoke;
      width:auto;
      
     
  }

  h1{
    text-align: center;
    text-transform: uppercase;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;

  }
  .container h2 p{
   color:black;
 }

.gallery{
  padding:20px;
   text-align: center;
   width:200px;
   opacity:.5;
   
   
  }
  .gallery:hover{
   opacity:1;
   background-color: white;
  
 }
.gallery_image{
   height: 100px;
   width: 100px; }

 .button-container{
   text-align: center;
 }
 .headline{
   text-align: center;
   text-transform: uppercase;
   font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
   color:white;
   background-color: teal;
   width: 400px;
   
 }

 .product-header{
   width: auto;
   display:flex;
   justify-content: center;
  
 }
 .buy{
   font-weight:bold;
   border-radius:15px;
   background-color:teal;
   color:white;
   


 }
 .buy:hover{
   cursor:pointer;
   opacity:.5;
 }

 /*Media Queries*/

 @media only screen and (max-width: 800px) {
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  
  }
}

@media only screen and (max-width: 400px) {
  .container {
    display: flex;
    flex-direction: column;
   
  
  }
}
* {
    box-sizing:border-box;
    margin:10px;
    padding:0;
}





li a{
  font-family:"Montserrat",sans-serif;
  font-weight:500;
  font-size:16px;
  color:white;
  text-decoration: none;
}


.header{
  display:flex;
  justify-content: space-between;
  align-items: center ;
  padding:30px 10%;
  background-color: teal;
 
}

.logo{
  cursor:pointer;
  margin-right: auto;
  
}

.kleco-nav{
  list-style-type: none;
}

.kleco-nav li{
  display: inline-block;
  padding:0px 20px;
}

.kleco-nav li a{
  transition: all 0.3s ease 0s;
}

.kleco-nav li a:hover{
  color:blueviolet;
}

footer {
    font-family:"Montserrat",sans-serif;
    padding: 20px;
    margin-top: 20px;
    color: white;
    text-align: center;
    font-size:20px;
    height: 300px;
    border:5px solid white;
    background-color: teal;
  }

  
  .social-logos li img {
    width: 5%;
    height: 5%;
  }
  
  .social-logos li {
    list-style-type: none;
    display: inline-block;
  }
  

  
